  view: CalendarView = CalendarView.Week;

  viewDate: Date = new Date();

  hourBlock: Number = 2;

  events: CalendarEvent[] = [
    // {
    //   start: startOfDay(new Date()),
    //   title: 'An event',
    //   //color: colors.yellow,
    // },
    // {
    //   start: addHours(startOfDay(new Date()), 2),
    //   end: new Date(),
    //   title: 'Another event',
    //   //color: colors.blue,
    // },
    // {
    //   start: addDays(addHours(startOfDay(new Date()), 2), 2),
    //   end: addDays(new Date(), 2),
    //   title: 'And another',
    //   //color: colors.red,
    // },
  ];

  hourColumns: WeekViewHourColumn[]; //holds days of the 7 week

  beforeWeekOrDayViewRender(event: CalendarWeekViewBeforeRenderEvent) {
    this.hourColumns = event.hourColumns; //is it showing as events?
    this.addSelectedDayViewClass();
    //draw the selected items
  }

  hourSegmentClicked(date: Date) {
    console.log("Clicked");
    //console.log(date);

    this.selectedDayViewDate = date;
    this.addSelectedDayViewClass();

    this.aptDates.push(date);

    //add unselect
  }

